<template>
  <div class="user-center" ref="userCenter">
    <h3 class="singer-name"  ref="singerName">
      <span class="back-btn" @click="handleBack"><i class="icon-back"></i></span>
      <div class="switches-wrapper">
        <div class="switches">
          <span class="favorite item" :class="{actived: !currentList}" @click="changeList(0)">收藏歌曲</span>
          <span class="heard item" :class="{actived: currentList}" @click="changeList(1)">最近听的</span>
        </div>
      </div>
    </h3>
    <div class="play-wrapper">
      <div ref="playBtn" class="play"  @click="randomPlayList">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
    </div>
    <div class="list-wrapper" ref="listWrapper">
      <scroll :data="currentList_1" :probeType="3" class="list" ref="List" v-if="currentList_1.length">
        <song-list :songDataes="currentList_1" @select="selectItem"></song-list>
      </scroll>
    </div>

    <div class="no-result-wrapper" v-if="!currentList_1.length">
      <no-result :title="noResultDesc"></no-result>
    </div>
  </div>
</template>

<script type="text/javascript">
  import Scroll from 'base/scroll/scroll'
  import {mapGetters, mapActions} from 'vuex'
  import SongList from 'base/song-list/song-list'
  import NoResult from 'base/no-result/no-result'
  export default {
    components: {
      Scroll,
      SongList,
      NoResult
    },
  	data () {
  		return {
        currentList: 0,
        noResultDesc: "暂时无歌曲\n请去添加..."
  		}
  	},
    computed: {
      ...mapGetters(['favoriteList', 'heardList'])
    },
    methods: {
      ...mapActions(['randomPlay','selectPlay']),
      handlePlayList(playList) {
          let bottom = playList.length ? '50px' : 0
          this.$refs.listWrapper.style.bottom = bottom
          this.$refs.List.refresh()
      },
      handleBack() {
        this.$router.go(-1)
      },
      changeList(index){
        this.currentList = index
        if (index === 1) {
          this.currentList_1 = this.heardList
        }else{
          this.currentList_1 = this.favoriteList
        }
      },
      randomPlayList() {
        this.randomPlay({
          list: this.currentList_1
        })
      },
      selectItem(item, index){
        this.selectPlay({
          list: this.currentList_1,
          index
        })
      }
    },
    created(){
      this.currentList_1 = this.favoriteList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"
  .user-center
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    background: white
    .singer-name
      color: white
      background: red
      font-size: $font-size-large-x
      line-height: 40px
      height: 40px
      position: relative
      width: 100%
      z-index:2
      text-align: center
      padding: 5px 0
      .back-btn
        color: white
        font-size: $font-size-large-x
        padding-left: 10px
        line-height: 40px
        left: 0
        position: absolute
        z-index: 2
        .icon-back
          line-height: 40px
          height: 40px
      .switches-wrapper
        font-size: 15px
        line-height: 40px
        position: absolute
        width: 100%
        .switches
          width: 200px
          box-sizing: border-box
          height: 30px
          line-height: 30px
          margin: 5px auto
          display: flex
          .favorite
            display: inline-block
            border: 1px solid rgba(233,233,233,.5)
            border-right: none
            border-radius: 5px 0 0 5px
            flex: 1
          .heard
            display: inline-block
            width: 95px
            border: 1px solid rgba(233,233,233,.5)
            border-radius: 0 5px 5px 0
            flex: 1
          .actived
            background: orange
    .play-wrapper
      padding: 20px 0
      
      background: red
      .play
        box-sizing: border-box
        width: 135px
        padding: 7px 0
        margin: 0 auto
        text-align: center
        border: 1px solid white
        color: white
        border-radius: 100px
        font-size: 0
        .icon-play
          display: inline-block
          vertical-align: middle
          margin-right: 6px
          font-size: $font-size-medium-x
        .text
          display: inline-block
          vertical-align: middle
          font-size: $font-size-small
    .list-wrapper
      position: fixed
      top: 122px
      bottom: 0
      width: 100%
      overflow: hidden
      .list
        height: 100%
</style>


